.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/*----------  Booked - Animations  ----------*/
/* Modal Fly In */
@keyframes booked-flyin {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

/* Datepicker Pop Flyin */
@keyframes booked-popflyin {
  0% {
    opacity: 0;
    transform: scale(0.95);
  }
  50% {
    opacity: 1;
    transform: scale(1.01);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

/* Modal Fly Out */
@keyframes booked-flyout {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.9);
  }
}

/* Hidden Custom Fields Pop Down */
@keyframes booked-popdown {
  0% {
    opacity: 0;
    transform: translateY(-5px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Modal Fade In */
@keyframes booked-fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Modal Fade Out */
@keyframes booked-fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 9;
  }
}

